<template>
  <div class="flash-page" v-scroll="scrollFn">
    <skeleton v-if="skeletonLoading" :nums="3"></skeleton>
    <div v-else>
      <Header class="flash-header" :class="{'header-bg': isFixed}">
        <img class="back-btn" v-if="!$route.query.browerFirstIn" slot="left" :src="$globalIcon.whiteBackIcon" alt />
        <div class="header-text" slot="title">限时购</div>
        <div class="header-share" slot="right" v-if="isShare" @click="doShare" :style="{backgroundImage: `url(${$globalIcon.whiteShareIcon})`}"></div>
      </Header>
      <div class="flash-warp">
        <div class="flash-warp-header"></div>
        <div class="flash-warp-list">
          <FlashItem v-for="(item,index) in shopList" :key="index" :info="item"/>
        </div>
      </div>
      <div class="footer-txt">
        <img v-if="!shopList.length" src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/goods/tipImg.png" alt="">
        <p>没有更多了</p>
      </div>
    </div>
  </div>
</template>

<script>
import skeleton from '@/components/skeleton'
import Header from '@/components/header'
import FlashItem from './component/flashitem'

import Url from '@/http/url'
import { mapGetters } from 'vuex'

export default {
  name: 'flashSaleList',
  components: {
    Header,
    skeleton,
    FlashItem,
  },
  data() {
    return {
      skeletonLoading: true,
      isShare: this.$util.isHaierApp(),
      isFixed: false,
      shopList: [],
    }
  },
  mounted () {
    this.getSaleByShop()
  },
  computed: {
    ...mapGetters(['getlocationInfo', 'mId'])
  },
  directives: {
    scroll: {
      inserted (el, binding) {
        el.addEventListener('scroll', (ev) => {
          console.log('flashsale scroll')
          binding.value(el.scrollTop)
        })
      }
    }
  },
  methods: {
    getSaleByShop() {
      this.$http.get(Url.flashSaleShop, {
        params: {
          provinceId: this.getlocationInfo.provinceId,
          cityId: this.getlocationInfo.cityId,
          districtId: this.getlocationInfo.districtId,
          streetId: this.getlocationInfo.townshipId,
        }
      }).then(({ data }) => {
        this.skeletonLoading = false
        let result = data.data || [];
        console.log(result)
        this.shopList = result
      })
    },
    scrollFn(scrollTop) {
      this.isFixed = scrollTop > 10
    },
    doShare() {
      this.$util.inAppShare({
        title: '今日爆款',
        url: location.href.indexOf('?') !== -1 ? location.href + '&shareId=' + this.mId : location.href + '?shareId=' + this.mId,
        desc: '爆款特价，限时直降，仅此一天，数量有限，抓紧抢购吧~'
      })
    },
  }
}
</script>

