                  <div><span class="product-tag">直降{{ item.lowestPrice ? (item.miniPrice - item.lowestPrice).toFixed(0) : (item.miniPrice - item.flashsalePrice).toFixed(0)}}元</span></div>
                  <p class="price-no"><small class="yuan">￥</small>{{(item.lowestPrice || item.flashsalePrice) | formatPriceInt}}<small>{{(item.lowestPrice  || item.flashsalePrice) | formatPriceFloat}}</small><del><small class="del-yuan">￥</small>{{item.miniPrice | formatPriceInt}}{{item.miniPrice|formatPriceFloat}}</del></p>
                </div>
                <div class="item-price-btn">
                  <p>已售<span>{{item.saleNum}}</span>件</p>
                  <h6>去购买</h6>
