<script>
import skeleton from '@/components/skeleton'
import Header from '@/components/header'
import FlashItem from './component/flashitem'
import Countdown from '@/components/countdown'
import Url from '@/http/url'
import mixins from '@/components/top-feedBack/mixins_inpage_scroll.js'
import { mapGetters } from 'vuex'

export default {
  name: 'flashSale',
  mixins: [mixins],
  components: {
    skeleton,
    Header,
    FlashItem,
    Countdown
  },
  data() {
    return {
      isShare: this.$util.isHaierApp(),
      skeletonLoading: true,
      isFixed: false,
      tuiShopList: [], // 限时购list
      daiesTab: [], // 日期tab
      curDay: '', // 当前日期
      timesTab: [], // 时刻tab
      curTime: '', // 当前时刻
      productList: [], // 活动商品list
      countTime: 0, // 倒计时秒数
      sysTime: 0, // 当前系统时间
      activeStatus: true, // 活动状态
      timeFixed: false, // 时间tab吸顶
      rem: 0,
      scrollTarget: '.flash-page', // 页面滚动元素
    }
  },
  mounted () {
    this.rem = this.getREM()
    this.getSaleByShop()
    this.fetchData()
  },
  activated() {
    const element = document.querySelector('.flash-page')
    if (element) this.option(element.scrollTop)
  },
  directives: {
    scroll: {
      inserted (el, binding) {
        el.addEventListener('scroll', (ev) => {
          console.log('flashsale scroll')
          binding.value(el.scrollTop)
        })
      }
    }
  },
  computed: {
    ...mapGetters(['getlocationInfo', 'mId'])
  },
  methods: {
    changeDay(item) {
      this.curDay = item.dayId
      let curDayIndex = this.daiesTab.findIndex(item => item.dayId === this.curDay)
      this.timesTab = this.daiesTab[curDayIndex].list || []
      this.curTime = this.timesTab[0].flashSaleId
      console.log(item)
      this.fetchData()
    },
    changeTimeTab(item) {
      this.curTime = item.flashSaleId
      console.log(item)
      this.fetchData()
    },
    fetchData() {
      this.$http.get(Url.flashSaleTitle, {
        params: {
          provinceId: this.getlocationInfo.provinceId,
          cityId: this.getlocationInfo.cityId,
          districtId: this.getlocationInfo.districtId,
          streetId: this.getlocationInfo.townshipId,
          from: 1,
        },
        cancelRepeat: true
      }).then(({ data }) => {
        this.skeletonLoading = false
        let result = data.data;
        console.log(result.dates)
        this.sysTime = result.systemTime
        result.dates && this.makeParamTab(result.dates)
      })
    },
    getSaleByShop() {
      this.$http.get(Url.flashSaleShop, {
        params: {
          provinceId: this.getlocationInfo.provinceId,
          cityId: this.getlocationInfo.cityId,
          districtId: this.getlocationInfo.districtId,
          streetId: this.getlocationInfo.townshipId,
        }
      }).then(({ data }) => {
        let result = data.data || [];
        console.log(result)
        this.tuiShopList = result.splice(0, 5) // [result[0]]
      })
    },
    makeParamTab(data) {
      this.daiesTab = data.map((item, index) => {
        let dateArr = item.date.split('/')
        return {
          ...item,
          dayId: 'd' + dateArr.join('_'),
          date: dateArr[1] + '月' + dateArr[2] + '日'
        }
      })

      if (!this.curDay && !this.curTime) { // 初始值
        let dayObject = this.getInitDayObject()
        this.curDay = dayObject.dayId
        this.timesTab = dayObject.list || []
        let timeObject = this.getInitTimeObject()
        this.curTime = timeObject.flashSaleId
        this.activeStatus = this.sysTime - timeObject.startTime > 0
        console.log(this.activeStatus, 'activeStatus')
        this.productList = timeObject.products
        this.countTime = this.activeStatus ? parseInt((timeObject.endTime - this.sysTime) / 1000) : parseInt((timeObject.startTime - this.sysTime) / 1000)
        // this.curDay = this.daiesTab[0].dayId
        // this.timesTab = this.daiesTab[0].list || []
        // this.curTime = this.timesTab[0].flashSaleId
        // this.activeStatus = this.sysTime - this.timesTab[0].startTime > 0
        // console.log(this.activeStatus, 'activeStatus')
        // this.productList = this.timesTab[0].products
        // this.countTime = this.activeStatus ? parseInt((this.timesTab[0].endTime - this.sysTime) / 1000) : parseInt((this.timesTab[0].startTime - this.sysTime) / 1000)
      } else { // 取上次的记录
        console.log('===>', this.curDay, this.curTime, this.daiesTab)
        if (this.daiesTab.length) {
          let curDayIndex = this.daiesTab.findIndex(item => item.dayId === this.curDay)
          if (curDayIndex === -1) {
            location.reload()
          }
          this.curDay = this.daiesTab[curDayIndex].dayId
          this.timesTab = this.daiesTab[curDayIndex].list || []
          if (!this.timesTab.length) {
            location.reload()
          }
          let curIndex = this.timesTab.findIndex(item => item.flashSaleId === this.curTime) !== -1 ? this.timesTab.findIndex(item => item.flashSaleId === this.curTime) : 0
          console.log(curDayIndex, curIndex, this.timesTab, this.productList)
          this.curTime = this.timesTab[curIndex].flashSaleId
          this.activeStatus = this.sysTime - this.timesTab[curIndex].startTime > 0
          this.productList = this.timesTab.find(item => item.flashSaleId === this.curTime) ? this.timesTab.find(item => item.flashSaleId === this.curTime).products : this.timesTab[0].products

          this.countTime = this.activeStatus ? parseInt((this.timesTab[curIndex].endTime - this.sysTime) / 1000) : parseInt((this.timesTab[curIndex].startTime - this.sysTime) / 1000)
        }
      }
    },
    /**
     * @description 获取初始化 day 实体
    */
    getInitDayObject() {
      let retObj = this.daiesTab[0] // 默认取第一个
      let _sysTime = new Date(this.sysTime)
      this.daiesTab.forEach(i => {
        let _date = new Date(i.date)
        if (_sysTime.getDate() === _date.getDate()) {
          retObj = i
        }
      })
      return retObj
    },
    /**
     * @description 获取初始化 time 实体
    */
    getInitTimeObject() {
      let retObj = this.timesTab[0] // 默认取第一个
      this.timesTab.forEach(i => {
        if (this.sysTime > i.startTime && this.sysTime < i.endTime) {
          retObj = i
        }
      })
      return retObj
    },
    doShare() {
      this.$util.gioTrackWithVariable('mall_flashSale_click_share')
      this.$util.inAppShare({
        title: '今日爆款',
        url: location.href.indexOf('?') !== -1 ? location.href + '&shareId=' + this.mId : location.href + '?shareId=' + this.mId,
        desc: '爆款特价，限时直降，仅此一天，数量有限，抓紧抢购吧~'
      })
    },
    goDetail(item, index) {
      let param = {
        productId: item.productId,
      }
      if (item.storeId) {
        param.visitStore = item.storeId
      }
      if (item.skku) {
        param.skku = item.skku
      }
      this.$util.gioTrackWithVariable('MB15614', {
        site_number: index + 1 + '',
        productId_var: item.productId + '',
      })

      this.$router.push({
        path: '/goodsDetail',
        query: param,
      })
    },
    countDownFn() {
      this.getSaleByShop()
      let timer = setTimeout(() => {
        clearTimeout(timer)
        this.fetchData()
      }, 1000)
    },
    option(scrollTop) {
      this.isFixed = scrollTop > 10
      if (!this.$refs.dayTab) return
      let _dayTop = this.$refs.dayTab.getBoundingClientRect().top // 发现好货日期 顶部距离
      if (_dayTop / this.rem < 0.92) {
        this.timeFixed = true
      } else {
        this.timeFixed = false
      }
      // showToTopFlag
      this.showToTopFlag = scrollTop > 200
    },
    goShop(id) {
      this.$router.push(`/shopDetail?storeId=${id}`)
    },
    goMore() {
      this.$router.push('/flashSaleList')
    },
    getREM() {
      let rem = parseInt(window.getComputedStyle(document.documentElement, null).getPropertyValue('font-size'))
      return rem
    },
  }
}
</script>

