<template>
  <div class="flash-item" @click="goDetail(info)">
    <div class="flash-img">
      <img :src="info.imageUrl" alt="">
    </div>
    <div class="flash-body">
      <div class="flash-title">
        <h4>{{info.productName}}</h4>
        <p><small v-for="(tag,idx) in info.sellPoint" :key="idx"><span v-if="idx">|</span>{{tag}}</small></p>
      </div>
      <div class="flash-price">
        <div class="flash-price-text"><small>￥</small><span>{{info.flashsalePrice | formatPriceInt}}</span><small>{{info.flashsalePrice | formatPriceFloat}}</small> <del v-if="info.miniPrice"><small>￥</small>{{info.miniPrice.toFixed(2)}}</del></div>
        <div class="flash-price-link"><a href="javascript:;">去购买</a></div>
      </div>
      <div class="flash-footer" @click.stop="goShop(info.storeId)">
        <img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/goodsDetail/shop.png" alt="">
        <p>{{info.storeName}}</p>
        <van-icon name="arrow" size=".24rem"/>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'flashItem',
  props: ['info'],
  methods: {
    goShop(id) {
      this.$router.push({
        path: '/shopDetail',
        query: {
          storeId: id
        }
      })
    },
    goDetail(item) {
      this.$router.push({
        path: '/goodsDetail',
        query: {
          productId: item.productId,
          visitStore: item.storeId
        },
      })
    }
  }
}
</script>

