<template>
  <div class="coutdown-warp" v-if="lang=='en'">
    <span v-if="hasDay">{{day}}</span><small v-if="hasDay">:</small>
    <span>{{hour}}</span><small>:</small>
    <span>{{min}}</span><small>:</small>
    <span>{{sec}}</span>
  </div>
  <div class="coutdown-warp" v-else-if="lang=='cn'">
    <span v-if="hasDay">{{day}}</span><small v-if="hasDay">天</small>
    <span>{{hour}}</span><small>时</small>
    <span>{{min}}</span><small>分</small>
    <span>{{sec}}</span><small>秒</small>
  </div>
</template>

<script>
export default {
  props: {
    time: {
      type: Number,
      default: 0
    },
    hasDay: {
      type: Boolean,
      default: false
    },
    lang: {
      type: String,
      default: 'en'
    },
    endTime: {
      type: Function,
      default: () => {}
    }
  },
  data() {
    return {
      leftTime: 0,
      interval: null,
      day: '--',
      hour: '--',
      min: '--',
      sec: '--',
    }
  },
  watch: {
    time: {
      immediate: true,
      handler(newValue, oldValue) {
        if (newValue !== oldValue) {
          clearInterval(this.interval);
          this.leftTime = this.time;
          if (this.leftTime > 0) {
            this.run()
          }
        }
      }
    }
  },
  methods: {
    computedTime() {
      this.day = this.toDoubleStr(Math.floor(this.leftTime / 3600 / 24))
      this.hour = this.toDoubleStr(Math.floor(this.hasDay ? (this.leftTime / 3600 % 24) : (this.leftTime / 3600)))
      this.min = this.toDoubleStr(Math.floor((this.leftTime / 60) % 60))
      this.sec = this.toDoubleStr(this.leftTime % 60)
      if (this.leftTime <= 0) {
        clearInterval(this.interval);
        this.endTime()
      }
      this.leftTime--;
    },
    run() {
      this.computedTime();
      this.interval = setInterval(this.computedTime, 1000)
    },
    toDoubleStr(num) {
      return num > 9 ? '' + num : '0' + num
    }
  },
}
</script>

<style lang="scss" scoped>
.coutdown-warp{
  display: inline-flex;
  justify-content: center;
  align-items: center;
}
</style>
